@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@400;500&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: Red Hat Text;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url(/Launch-Countdown/static/media/bg-stars.166881a1.svg) no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  background-color:  hsl(289deg 36% 12%);
}

.header {
  font-family: Red Hat Text;
  justify-self: start;
  align-self: unset;
  margin-top: 20vh;
  font-size: 2vw;
  letter-spacing: 1vw;
  color: hsl(237, 18%, 59%);
}

.card-row{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 10vh;
  grid-gap: 2vw;
}

@media all and (min-width: 600px) {
  .header {
    font-size: 1.1vw;
  }
}


.card{
    border: 1px hidden;
    width: 16vw;
    font-family: Red Hat Text;
    margin-bottom: 20vh;
}

.card-time{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px hidden;
    font-size: 10vw;
    font-weight: 500;
    height: 16vw;
    cursor: pointer;
    background: linear-gradient( rgba(68, 40, 68, 0.534) , rgba(93, 24, 133, 0.562) );
    color: hsl(345, 60%, 50%);
    border-radius: 1vw;
    box-shadow: 0 1vh 0px rgba(0, 0, 0, 0.253);
}

.card-time > span {
    transition: 0.5s;
}

.card-text {
    text-align: center;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 1.5vw;
    letter-spacing: 0.6vw;
    font-weight: 500;
    margin-top: 3vh;
    color: hsl(237, 18%, 59%);
    transition: 0.5s;
}

.card:hover > .card-time > span , .card:hover .card-text {
    transform: scale(1.2);
}

@media all and (min-width: 600px) {
    .card{
        width: 12vw;
    }

    .card-time{
        height: 12vw;
        font-size: 8vw;
    }

    .card-text{
        font-size: 0.8vw;
        letter-spacing: 0.5vw;
    }
}

.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    align-self: end;
    width: 100%;
    height: 20vh;
    background: url(/Launch-Countdown/static/media/pattern-hills.815d688f.svg) no-repeat;
    background-size: cover;
}

.image > img{
    margin-left: 3vw;
    transition: 0.5s;
}

.image > img:hover{
    transform: scale(1.1);
    -webkit-filter: invert(48%) sepia(63%) saturate(624%) hue-rotate(299deg) brightness(100%) contrast(97%) ;
